.App {
  background-color: #282c34;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  color: white;
}

.App-header {
  font-size: calc(10px + 2vmin);
}

h1 {
  margin: 0px;
}

img {
  width: 50%;
}

.name {
  letter-spacing: 3px;
}

.fun {
  letter-spacing: -3px;
  color: red;
}

input {
  padding: 10px;
  padding-right: 200px;
}

a {
  color: pink;
  text-decoration: none;
}

a:visited {
  color: pink;
}